
@import "../../scss/_global.scss";

#faq {
  @extend %indent-p;

  .title {
    h2 {
      @media (max-width: 425px) {
        font-size: 24px;
        line-height: 30px;
      }
    }
  }

  .accordions {
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @media (max-width: 768px) {
      gap: 30px;
    }

    @media (max-width: 576px) {
      gap: 18px;
    }
  }

  .accordion-menu {
    @extend %shadow-block;
    border-radius: 10px;
    padding: 24px 60px;
    cursor: pointer;
    user-select: none;
    min-height: 100px;
    display: flex;
    align-items: center;

    gap: 15px;
    justify-content: space-between;

    @media (max-width: 768px) {
      padding: 14px;
      min-height: 70px;
    }

    h3 {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 31px;
      color: $gray;

      @media (max-width: 768px) {
        font-size: 16px;
        line-height: 19px;
      }
    }
  }

  .accordion-content {
    height: 0;
    overflow: hidden;

    &__wrap {
      padding: 60px 60px calc(60px - 40px);

      @media (max-width: 768px) {
        padding: 30px 10px 0;
      }

      @media (max-width: 576px) {
        padding: 18px 10px 0;
      }
    }

    p {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      line-height: 31px;
      color: $gray;

      @media (max-width: 768px) {
        font-size: 16px;
        line-height: 19px;
      }
    }
  }
}
